.address-form-container {
  max-width: 800px;
  margin: 0 auto;
  font-family: Arial, sans-serif;
}

h2 {
  margin-top: 30px;
  margin-bottom: 15px;
  color: #333;
}

/* -------------------------------
   Vista compacta
--------------------------------*/
.address-compact {
  border: 1px solid #ddd;
  padding: 10px 15px;
  margin-bottom: 10px;
  border-radius: 8px;
  background-color: #fafafa;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 15px;
}

.address-compact.selected {
  background-color: #d1e7dd;
  border-color: #0f5132;
  color: #0f5132;
}

.address-compact.selected input[type="checkbox"] {
  display: none;
}

.address-compact div {
  font-size: 14px;
  color: #555;
}

.address-compact strong {
  display: block;
  margin-bottom: 5px;
}

.predeterminated-tag {
  color: #ffc107;
  margin-left: 5px;
  font-size: 14px;
}

.address-compact label {
  display: flex;
  align-items: center;
  gap: 5px;
  font-size: 13px;
  color: #333;
}

.edit-btn {
  background-color: #4caf50;
  color: white;
  border: none;
  padding: 5px 10px;
  border-radius: 5px;
  cursor: pointer;
  font-size: 13px;
}

.edit-btn:hover {
  background-color: #45a049;
}

/* -------------------------------
   Formulario desplegable
--------------------------------*/
.address-form {
  border: 1px solid #ccc;
  padding: 15px;
  margin-bottom: 10px;
  border-radius: 8px;
  background-color: #fff;
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}

.address-form input {
  padding: 8px 10px;
  border-radius: 5px;
  border: 1px solid #ccc;
  font-size: 14px;
  flex: 1 1 200px;
  max-width: 48%;
  box-sizing: border-box;
}

.address-buttons {
  display: flex;
  gap: 10px;
  margin-top: 10px;
  flex-basis: 100%;
}

.default-btn { background-color: #2196f3; color: white; cursor: pointer;}
.default-btn.active { background-color: #0b79d0; }
.default-btn:hover { background-color: #1976d2; }

.save-btn { background-color: #4caf50; color: white; cursor: pointer; }
.save-btn:hover { background-color: #45a049; }

.cancel-btn { background-color: #f44336; color: white; cursor: pointer; }
.cancel-btn:hover { background-color: #da190b; }

.delete-btn { background-color: #ff9800; color: white; cursor: pointer; }
.delete-btn:hover { background-color: #fb8c00; }

.add-btn {
  margin-top: 10px;
  padding: 8px 15px;
  background-color: #673ab7;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.add-btn:hover { background-color: #5e35b1; }
