.register {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
  box-sizing: border-box;
}

/* === REGISTER BOX === */
.register-container {
  background: white;
  padding: 35px 40px;
  border-radius: 22px;
  box-shadow: 0 4px 14px rgba(0, 0, 0, 0.15);
  width: 360px;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  animation: fadeIn 0.3s ease both;
}

.register-container h2 {
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 18px;
  color: #333;
}

/* === INPUTS === */
.register-container input {
  width: 100%;
  margin-bottom: 14px;
  padding: 12px;
  border: 1px solid #ddd;
  border-radius: 10px;
  font-size: 15px;
  box-sizing: border-box;
  transition: 0.2s;
}

.register-container input:focus {
  border-color: #1f2937;
  outline: none;
}

/* === CHECKBOX EN LINEA CENTRADO === */
.checkbox-autologin {
  display: flex;
  align-items: center; /* centra verticalmente el checkbox con el texto */
  gap: 8px;
  font-size: 14px;
  width: 100%;
  margin-bottom: 14px;
}

.checkbox-autologin input {
  width: 16px;
  height: 16px;
  margin: 0; /* eliminamos margen para que quede perfectamente centrado */
}

/* === BOTÓN REGISTER === */
.register-container button {
  width: 100%;
  padding: 12px 0; /* mismo alto que antes */
  margin-top: 5px;
  background-color: #1f2937;
  color: white;
  border: none;
  border-radius: 10px;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
  transition: 0.25s;
}

.register-container button:hover {
  background-color: #000000;
  transform: translateY(-1px);
}

/* === VOLVER A LOGIN === */
.back-to-login {
  width: 100%;
  padding: 12px 0; /* igual que el botón de registrarse */
  margin-top: 10px;
  border-radius: 10px;
  background: #e4e9f0;
  color: #333;
  font-size: 16px;
  font-weight: bold;
  text-decoration: none;
  display: inline-block;
  text-align: center;
  transition: 0.2s;
}

.back-to-login:hover {
  background: #d3dae3;
}

/* === MENSAJES === */
.error {
  width: 100%;
  border-left: 4px solid #e63946;
  padding: 10px;
  color: #b50909;
  border-radius: 6px;
  margin-bottom: 10px;
}

.register-message {
  width: 100%;
  border-left: 4px solid #32cd32;
  padding: 10px;
  color: #1f7a1f;
  border-radius: 6px;
  margin-bottom: 10px;
}

/* === RESPONSIVE === */
@media (max-width: 420px) {
  .register-container {
    width: 100%;
    padding: 22px;
  }
}

/* === ANIMACIÓN === */
@keyframes fadeIn {
  from { opacity: 0; transform: translateY(6px); }
  to   { opacity: 1; transform: translateY(0); }
}
