/* === TOP BAR === */
.top-bar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 60px;
  background-color: #1f2937; /* azul grisáceo oscuro */
  color: white;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding: 0 15px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.2);
  z-index: 1000;
  box-sizing: border-box;
}

/* contenedor de los iconos a la derecha */
.top-bar-right {
  display: flex;
  align-items: center;
  gap: 10px;
}

/* forzar que el form se comporte como un flex container centrado */
.logout-form {
  display: flex;
  align-items: center;
  margin: 0;     /* evitar márgenes por defecto que desalineen */
  padding: 0;
}

/* Botón de icono: hace que el icono esté exactamente centrado dentro del botón */
.icon-btn {
  display: inline-flex;        /* importante: inline-flex permite centrado del contenido */
  align-items: center;
  justify-content: center;
  width: 40px;                 /* tamaño cuadrado cómodo para iconos */
  height: 40px;
  background: none;
  border: none;
  cursor: pointer;
  padding: 6px;
  border-radius: 8px;
  transition: background 0.2s, transform 0.08s;
  color: white;
  box-sizing: border-box;
}

.icon-btn:hover {
  background: rgba(255, 255, 255, 0.08);
  transform: translateY(-1px);
}

/* Botón de cerrar sesión con color de fondo específico (mantener contraste) */
.logout-form .icon-btn {
  background-color: #e3342f;
  color: white;
}

.logout-form .icon-btn:hover {
  background-color: #c53027;
}

/* === HOME CONTENT: evitar que se quede debajo de la top bar === */
.home-container .home-content {
  padding-top: 80px; /* altura de la top-bar + margen (ajusta si cambias la altura) */
}

/* === RESPONSIVE === */
@media (max-width: 480px) {
  .top-bar {
    flex-direction: column;
    align-items: flex-start;
    height: auto;
    padding: 10px;
  }

  .home-container .home-content {
    padding-top: 110px;
  }

  .icon-btn {
    width: 36px;
    height: 36px;
  }
}
