/* === LOGIN PAGE === */
.login {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
  box-sizing: border-box;
}

/* === LOGIN BOX === */
.login-container {
  background: white;
  padding: 35px 40px;
  border-radius: 22px;
  box-shadow: 0 4px 14px rgba(0, 0, 0, 0.15);
  width: 330px;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  animation: fadeIn 0.3s ease both;
}

.login-container h2 {
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 18px;
  color: #333;
}

/* === INPUTS === */
.login-container input {
  width: 100%;
  margin-bottom: 14px;
  padding: 12px;
  border: 1px solid #ddd;
  border-radius: 10px;
  font-size: 15px;
  box-sizing: border-box;
  transition: 0.2s;
}

.login-container input:focus {
  border-color: #1f2937;
  outline: none;
}

/* === BOTÓN LOGIN === */
.login-container button {
  width: 100%;
  padding: 12px;
  margin-top: 5px;
  background-color: #1f2937;
  color: white;
  border: none;
  border-radius: 10px;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
  transition: 0.25s;
}

.login-container button:hover {
  background-color: #000000;
  transform: translateY(-1px);
}

/* === LINKS ABAJO === */
.login-actions {
  width: 100%;
  margin-top: 16px;
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.login-link {
  color: #1f2937;
  font-size: 14px;
  text-decoration: none;
}

.login-link:hover {
  text-decoration: underline;
}

/* === MENSAJES === */
.error {
  width: 100%;
  background: #ffe5e5;
  border-left: 4px solid #e63946;
  padding: 10px;
  color: #b50909;
  border-radius: 6px;
  margin-bottom: 10px;
}

.login-message {
  width: 100%;
  background: #e6ffe8;
  border-left: 4px solid #32cd32;
  padding: 10px;
  color: #1f7a1f;
  border-radius: 6px;
  margin-bottom: 10px;
}

/* === RESPONSIVE === */
@media (max-width: 420px) {
  .login-container {
    width: 100%;
    padding: 22px;
  }
}

/* === ANIMACIÓN === */
@keyframes fadeIn {
  from { opacity: 0; transform: translateY(6px); }
  to   { opacity: 1; transform: translateY(0); }
}
